.hljs-template-variable,
.hljs-tag .hljs-attr {
  color: #008080; }
.hljs-string,
.hljs-doctag {
  color: #d14; }
.hljs-title,
.hljs-section,
.hljs-selector-id {
  color: #900;
  font-weight: bold; }
.hljs-subst {
  font-weight: normal; }
.hljs-type,
.hljs-class .hljs-title {
  color: #458;
  font-weight: bold; }
.hljs-tag,
.hljs-name,
.hljs-attribute {
  color: #000080;
  font-weight: normal; }
.hljs-regexp,
.hljs-link {
  color: #009926; }
.hljs-symbol,
.hljs-bullet {
  color: #990073; }
.hljs-built_in,
.hljs-builtin-name {
  color: #0086b3; }
.hljs-meta {
  color: #999;
