.sidebar {
  width: 290px;
  float: right;
}
.sidebar .personal-information {
    background: #fff;
    border-radius: 5px;
}
.sidebar .personal-information .top {
      padding: 10px;
      background: #f6f6f6;
}
.sidebar .personal-information .info {
      padding: 10px;
}
.sidebar .personal-information .info img {
        display: inline-block;
        width: 50px;
        height: 50px;
        background: #f7f7f7;
        -o-object-fit: cover;
           object-fit: cover;
}
.sidebar .personal-information .info .nickname {
        margin-left: 15px;
        color: #333;
        cursor: pointer;
}
.sidebar .personal-information .info a {
        display: inline-block;
        vertical-align: middle;
}
.sidebar .personal-information .publish-topic {
      padding: 0 0 10px 10px;
}
.sidebar .personal-information .publish-topic a {
        display: inline-block;
        position: relative;
        margin: 5px 0 0 0;
        padding: 5px 10px;
        background: #80bd01;
        color: #fff;
        border-radius: 5px;
        font-weight: 500;
        border: 1px solid #80bd01;
}
.sidebar .personal-information .publish-topic a:after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: #000;
          border-radius: 5px;
          opacity: 0;
          -webkit-transition: opacity .1s linear;
          transition: opacity .1s linear;
}
.sidebar .personal-information .publish-topic a:hover:after {
          opacity: .05;
}
.sidebar .personal-information .tourist-box {
      padding: 10px;
}
.sidebar .personal-information .cnode {
      color: #000;
}
.sidebar .personal-information .tourist {
      margin-top: 15px;
}
.sidebar .personal-information .tourist a {
        color: #2d8cf0;
}
.sidebar .personal-information .tourist a:hover {
          text-decoration: underline;
}
.sidebar .pay {
    margin-top: 15px;
    padding: 10px;
    border-radius: 5px;
    background: #fff;
}
.sidebar .pay img {
      width: 250px;
      height: 250px;
      -o-object-fit: cover;
         object-fit: cover;
}
.sidebar .scann-qrcode {
    margin-top: 15px;
    padding: 10px;
    background: #fff;
    border-radius: 5px;
    text-align: center;
}
.sidebar .scann-qrcode .scann-qrcode-title {
      padding: 10px;
      margin-bottom: 10px;
      background: #f6f6f6;
}
.sidebar .scann-qrcode img {
      width: 250px;
      height: 250px;
      pointer-events: none;
}
