.unique-topics-list li {
  overflow: hidden;
  padding: 10px;
}
.unique-topics-list li:not(:nth-last-child(1)) {
    border-bottom: 1px solid #f6f6f6;
}
.unique-topics-list li:hover {
    background: #eee;
}
.unique-topics-list .avatar {
  float: left;
}
.unique-topics-list .avatar img {
    width: 35px;
    height: 35px;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 2px;
}
.unique-topics-list .reply-view {
  float: left;
  width: 70px;
  text-align: center;
  font-size: 12px;
  margin: 9px 0 0 20px;
}
.unique-topics-list .tag {
  float: left;
  margin-top: 7px;
  font-size: 12px;
  color: #999;
  background: #e5e5e5;
  padding: 2px 4px;
  border-radius: 3px;
  font-weight: 500;
}
.unique-topics-list .tag.good, .unique-topics-list .tag.top {
    background: #80bd01;
    color: #fff;
}
.unique-topics-list .title {
  float: left;
  display: inline-block;
  width: calc(100% - 230px);
  margin: 6px 0 0 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 16px;
}
.unique-topics-list .title:hover {
    text-decoration: underline;
    color: #333;
}
.unique-topics-list .last-reply-time {
  float: right;
  font-size: 12px;
  margin-top: 8px;
}
